const list = [
  {
    imageSrc: '',
    label: '照片',
    handle: 'handleUserUploadImage'
  }, {
    imageSrc: '',
    label: '个人数据',
  }, {
    imageSrc: '',
    label: '打卡记录',
  },
];

export default {
  props: {
    userInputValue: {
      type: String,
      value: '',
    }
  },
  data() {
    return {
      list,
      footerActive: false,
      cloneUserInputValue: '',
    }
  },
  mounted() {
    this.cloneUserInputValue = this.userInputValue;
  },
  methods: {
    handleToggleMenus() {
      this.footerActive = !this.footerActive;
    },
    handleUserInput(e) {
      const {
        detail: { value },
      } = e;

      if (!value) {
        return
      }

      this.$emit('userInput', value);
      this.cloneUserInputValue = '';
    },
    handleUserUploadImage() {
      uni.chooseImage({
        count: 1,
        success: (chooseImageRes) => {
          this.$emit("userUploadImage", chooseImageRes);
        }
      });
    },
  },
}
